body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 20px;
  background: #e8f5e9;
  color: #333;
}

h1,
h2,
h3 {
  margin: 15px 0;
  color: #222;
  font-weight: 600;
  text-align: left;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: flex-start;
  gap: 20px;
  background: #45b39d;
  padding: 12px 18px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.navbar a {
  color: #fff;
  text-decoration: none;
  font-weight: 500;
}

.navbar a:hover {
  color: #d4efdf;
}

.navbar .active {
  font-weight: bold;
  text-decoration: underline;
}

/*  Dashboard Layout */
.dashboard {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 22px;
  margin-bottom: 35px;
}

.dashboard-tile {
  background: #fff;
  padding: 18px;
  border-radius: 8px;
  text-align: center;
}

.dashboard-tile h3 {
  margin-bottom: 8px;
  font-size: 15px;
  color: #666;
}

.dashboard-number {
  font-size: 24px;
  font-weight: bold;
  color: #2980b9;
}

/* Charts  */
.chart-container {
  background: #fff;
  padding: 12px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.chart-container h3 {
  margin-bottom: 6px;
  font-size: 14px;
  color: #444;
}

/*  Users Section  */
.users-container {
  background: #fff;
  padding: 16px;
  border-radius: 8px;
}

/*  Search + Sort Controls  */
.users-actions {
  display: flex;
  gap: 12px;
  margin: 12px 0 18px 0;
}

.users-actions input,
.users-actions select {
  padding: 6px 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 13px;
  background: #fafafa;
}

.users-actions input:focus,
.users-actions select:focus {
  outline: none;
  border-color: #45b39d;
  background: #fff;
}

/*  Table Styling  */
table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 12px;
}

th,
td {
  padding: 8px;
  border-bottom: 1px solid #eee;
  text-align: left;
  font-size: 13px;
}

th {
  background: #f0f3f5;
  color: #444;
}

tr:hover {
  background: #f9fbff;
}

/*  Buttons */
button {
  cursor: pointer;
}

table button {
  padding: 4px 8px;
  border: none;
  background: #2ecc71;
  color: white;
  border-radius: 4px;
  font-size: 12px;
}

table button:hover {
  background: #27ae60;
}

.pagination {
  display: flex;
  justify-content: center;
  gap: 8px;
}

.pagination button {
  padding: 5px 10px;
  border: none;
  background: #45b39d;
  color: #fff;
  border-radius: 4px;
  font-size: 12px;
}

.pagination button:disabled {
  background: #bbb;
  cursor: not-allowed;
}

/* ===== User Form  */
.user-form {
  display: flex;
  gap: 8px;
  margin-bottom: 15px;
}

.user-form input {
  padding: 5px 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.user-form button {
  padding: 5px 10px;
  border: none;
  background: #27ae60;
  color: #fff;
  border-radius: 4px;
}

.user-form button:hover {
  background: #229954;
}

/* Avatar Images */
td img {
  border: 1px solid #ddd;
  object-fit: cover;
  border-radius: 50%;
}

/* Modal Styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  min-width: 300px;
  text-align: center;
}

